.header {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    background-color: white;
    border-bottom: #c3e9df;
}

.btn {
    list-style: none;
    padding: 5px;
    color: #2e937a;
    text-decoration: none;
    font-size: 16px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    border-radius: 25px;
    margin: 0 20px;
}

.list {
    list-style: none;
}



.btn:hover {
    background: linear-gradient(360deg, rgb(247, 245, 245), rgb(255, 255, 255), #ffffff);
}

.wrapperButton {
    display: flex;
    margin: 0;
    padding: 0;
}

.wrapperNav{
    display: flex;
}

